
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

h1, h2 {
  font-weight: normal;
  margin: 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
  text-decoration: underline;
}
.layout {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
}
.layout__sidebar {
  width: 200px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  padding: 20px 20px;
}
.layout__content {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  padding: 20px 20px;
}
.header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  background: #417690;
  margin: 0;
  padding: 8px;
  font-weight: 400;
  color: #fff;
}
.feed-list__feed {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin: 0;
  height: 40px;
  padding: 0 8px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-weight: 400;
  border-bottom: solid 1px #aaaaaa;
}
.feed-list__feed:hover {
  background: #e1e1e8;
}
.messages {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  min-height: 100%;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-box-pack: left;
      -ms-flex-pack: left;
          justify-content: left;
  /*border: solid 1px silver;*/
}
.messages--loading {
  opacity: 0.7;
}
.messages__message {
  width: 50%;
  /*display: inline-block;*/
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  /*float: left;*/
  padding: 5px;
}
.messages__message:only-of-type {
  width: 100%;
  /*display: inline-block;*/
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  /*float: left;*/
  padding: 10px;
}
.message {
}
.message__header {
}
.message__header--blacklist {
  background: #2C2C2C;
}
.message__header--favorites {
  background: goldenrod;
}
.message__duplicates {

  background: silver;
  padding: 5px;
  font-size: 12px;
}
.message__text {
  padding: 5px;
  white-space: pre-wrap; /* css-3 */
  white-space: -moz-pre-wrap; /* Mozilla, since 1999 */
  white-space: -pre-wrap; /* Opera 4-6 */
  white-space: -o-pre-wrap; /* Opera 7 */
  word-wrap: break-word; /* Internet Explorer 5.5+ */
  border: solid 1px silver;
  /*max-height: 100px;*/
  overflow: hidden;
}
.message__new {
  display: inline-block;
  border-radius: 5px;
  width: 10px;
  height: 10px;
  background: red;
  margin: 0 5px;
}
.message__text--opened {
  max-height: 100%;
  background: #e1e1e8;
}
.header a {
  color: #fff;
  text-decoration: underline;
}
.highlighted_word {
  color: red !important;
}
.arrow-down {
  display: inline-block;
  padding: 5px;
}
.arrow-down:before {
  content: " ";
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  display: block;
  border-top: 5px solid #fff;
}
.arrow-down:hover:before {
  border-top: 5px solid #d43f3a;
}
.dropdown {
  position: relative;
  display: inline-block;
}
.dropdown-content {
  color: #000000;
  display: none;
  position: absolute;
  right: 0;
  background-color: #f9f9f9;
  min-width: 160px;
  -webkit-box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 10px;
  z-index: 1;
}
.dropdown-link {
  color: #417690;
  text-decoration: underline;
  cursor: pointer;
}
.dropdown:hover .dropdown-content {
  display: block;
}
.feed-list__feed-counter {
  background: #417690;
  text-align: center;
  min-width: 20px;
  height: 20px;
  padding: 2px;
  color: #ffffff;
}
.loader {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  height: 100%;
  display: none;
  text-align: center;
}
.loader__spinner {
  position: -webkit-sticky;
  position: sticky;
  top: 300px;
  margin: 0 auto;
  border: 10px solid #333333; /* Light grey */
  border-top: 10px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 30px;
  height: 30px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
  z-index: 9999;
}
.messages--loading .loader {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
@-webkit-keyframes spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
